import BankFunctionService from '@/services/bank/BankFunctionService'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let BankFunctionTableMixin = {
  mixins: [DisabledClassMixin, LoadingMixin, ListLayoutMixin],
  props: {
    defaultBankFunctionTableFilter: {
      type: String,
      default: null
    },
    defaultBankFunctionTableOrderBy: {
      type: String,
      default: null
    },
    disabled: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      bankFunctionTableData: [],
      bankFunctionTableFilterTimer: null,
      bankFunctionTableFilter: this.defaultBankFunctionTableFilter,
      bankFunctionTableOrderBy: this.defaultBankFunctionTableOrderBy,
      bankFunctionTableContainer: 'loading-container',
      bankFunctionTableBusy: false,
      bankFunctionTableTotalRows: 0,
      bankFunctionTableCurrentPage: 1,
      bankFunctionTablePerPage: 10
    }
  },
  computed: {
    bankFunctionTableNoDataText: function () {
      return this.bankFunctionTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    bankFunctionTableTotalPages: function () {
      return Math.ceil(this.bankFunctionTableTotalRows / this.bankFunctionTablePerPage)
    },
    bankFunctionTablePagingInfo: function () {
      return this.pagingInfo(this.bankFunctionTableCurrentPage, this.bankFunctionTablePerPage, this.bankFunctionTableTotalRows)
    }
  },
  methods: {
    loadBankFunctionTableData (loadingContainerId) {
      let self = this
      this.bankFunctionTableBusy = true
      this.bankFunctionTableContainer = loadingContainerId || this.bankFunctionTableContainer
      this.startLoading(this.bankFunctionTableContainer)
      BankFunctionService.bankFunctions({
        pageIndex: this.bankFunctionTableCurrentPage - 1,
        pageSize: this.bankFunctionTablePerPage,
        filter: this.bankFunctionTableFilter,
        orderByField: this.bankFunctionTableOrderBy,
        disabled: this.disabled
      }).then(function (response) {
        self.stopLoading(self.bankFunctionTableContainer)
        self.bankFunctionTableBusy = false
        self.bankFunctionTableTotalRows = response.data.totalRows
        self.bankFunctionTableData = response.data.functionList || []
      }).catch(function (error) {
        self.stopLoading(self.bankFunctionTableContainer)
        self.bankFunctionTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshBankFunctionTable () {
      this.loadBankFunctionTableData()
    },
    filterBankFunctionTable (filter) {
      let self = this
      if (this.bankFunctionTableFilter !== filter) {
        clearTimeout(this.bankFunctionTableFilterTimer)
        this.bankFunctionTableFilterTimer = setTimeout(() => {
          self.bankFunctionTableFilter = filter
          self.loadBankFunctionTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.bankFunctionTableFilterTimer)
      }
    },
    orderBankFunctionTable (orderBy, active) {
      this.bankFunctionTableOrderBy = (active ? '' : '-') + orderBy
      this.loadBankFunctionTableData()
    }
  },
  watch: {
    bankFunctionTableCurrentPage: function () {
      this.refreshBankFunctionTable()
    },
    bankFunctionTablePerPage: function () {
      this.refreshBankFunctionTable()
    }
  }
}

export default BankFunctionTableMixin
